/* --- Global Reset and Base Styles --- */
:root {
    --color-primary: #727070ff;
    --color-secondary: #9BA1B2ff;
    --color-text: #E7E8ECff;
    --color-glitch: #F7F8F8ff;
    --color-fade: #BABABDff;
    --font-mono: 'Orbitron', 'Space Mono', 'Courier New', monospace;
}

* { box-sizing: border-box; }

body {
    background-color: var(--color-primary);
    color: var(--color-text);
    font-family: var(--font-mono);
    margin: 0;
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    overflow-x: hidden;
    background: linear-gradient(180deg, var(--color-primary) 0%, var(--color-fade) 100%);
}


html {
    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
}

/* --- Full-Screen Block Structure --- */
.full-screen-block {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    scroll-snap-align: start;
    overflow: hidden;
    text-align: center;
}

/* --- Scanline and Static Effects --- */
.scanline-overlay {
    position: absolute; top: 0; left: 0; width: 100%; height: 100%;
    pointer-events: none; z-index: 5;
    background: repeating-linear-gradient(
        transparent 0,
        rgba(255, 255, 255, 0.1) 1px,
        transparent 2px
    );
    opacity: 0.4;
}

.static-overlay {
    position: absolute; top: 0; left: 0; width: 100%; height: 100%;
    pointer-events: none; z-index: 10;
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAIAAAACCAYAAABytg0kAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAB3RJTUUH5QgCDCYxKq8V5AAAAAAABl0RVh0Q3JlYXRpb24gVGltZQAwOC8wNC8yMjA5Mjo3MDowMDAwAAAAACXRFWHRTb2Z0d2FyZQBNYWRlIHdpdGggR0lNUCAyLjEwLjgAAAAASUVORK5CYII=');
    opacity: 0.2;
    animation: static-flicker 0.2s infinite;
}

@keyframes static-flicker {
    0% { opacity: 0.2; } 50% { opacity: 0.25; } 100% { opacity: 0.2; }
}

/* --- Digital Frame --- */
.digital-frame {
    position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);
    width: 80%; height: 70%;
    border: 1px solid var(--color-secondary);
    box-shadow: 0 0 10px var(--color-secondary), 0 0 20px rgba(155, 161, 178, 0.4);
    opacity: 0.3;
    pointer-events: none; z-index: 15;
    animation: slow-drift 10s infinite alternate;
}

@keyframes slow-drift {
    0% { transform: translate(-50%, -50%) rotate(0.5deg); }
    100% { transform: translate(-50%, -50%) rotate(-0.5deg); }
}

/* --- Geometric Shapes --- */
.geometric-shape {
    position: absolute;
    border: 1px solid var(--color-secondary);
    opacity: 0.2;
    pointer-events: none;
    z-index: 8;
}

.circle-1 {
    width: 300px; height: 300px;
    border-radius: 50%;
    top: 5%; right: 8%;
    box-shadow: 0 0 20px rgba(155, 161, 178, 0.3);
    animation: float-1 8s ease-in-out infinite;
}

.circle-2 {
    width: 150px; height: 150px;
    border-radius: 50%;
    bottom: 8%; left: 5%;
    animation: float-2 6s ease-in-out infinite;
}

.triangle {
    width: 0; height: 0;
    border-left: 100px solid transparent;
    border-right: 100px solid transparent;
    border-bottom: 173px solid var(--color-secondary);
    opacity: 0.15;
    top: 10%; left: 3%;
    animation: rotate-slow 20s linear infinite;
}

.square {
    width: 120px; height: 120px;
    border: 1px solid var(--color-secondary);
    bottom: 5%; right: 5%;
    transform: rotate(45deg);
    animation: pulse-shape 4s ease-in-out infinite;
}

@keyframes float-1 {
    0%, 100% { transform: translate(0, 0); }
    50% { transform: translate(-20px, -30px); }
}

@keyframes float-2 {
    0%, 100% { transform: translate(0, 0); }
    50% { transform: translate(30px, 20px); }
}

@keyframes rotate-slow {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@keyframes pulse-shape {
    0%, 100% { opacity: 0.15; transform: rotate(45deg) scale(1); }
    50% { opacity: 0.25; transform: rotate(45deg) scale(1.1); }
}

/* --- Grid Pattern Overlay --- */
.grid-overlay {
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    background-image:
        linear-gradient(rgba(155, 161, 178, 0.1) 1px, transparent 1px),
        linear-gradient(90deg, rgba(155, 161, 178, 0.1) 1px, transparent 1px);
    background-size: 40px 40px;
    opacity: 0.3;
    pointer-events: none;
    z-index: 6;
    animation: grid-drift 15s linear infinite;
}

@keyframes grid-drift {
    0% { transform: translate(0, 0); }
    100% { transform: translate(40px, 40px); }
}

/* --- Crosshair/Target Elements --- */
.crosshair {
    position: absolute;
    width: 60px; height: 60px;
    border: 1px solid var(--color-secondary);
    border-radius: 50%;
    opacity: 0.25;
    pointer-events: none;
    z-index: 11;
}

.crosshair::before,
.crosshair::after {
    content: '';
    position: absolute;
    background: var(--color-secondary);
}

.crosshair::before {
    width: 2px; height: 100%;
    left: 50%; top: 0;
    transform: translateX(-50%);
}

.crosshair::after {
    width: 100%; height: 2px;
    top: 50%; left: 0;
    transform: translateY(-50%);
}

.crosshair-1 { top: 15%; right: 15%; animation: crosshair-pulse 3s ease-in-out infinite; }
.crosshair-2 { bottom: 15%; left: 15%; animation: crosshair-pulse 3s ease-in-out infinite 1.5s; }

@keyframes crosshair-pulse {
    0%, 100% { transform: scale(1); opacity: 0.25; }
    50% { transform: scale(1.2); opacity: 0.4; }
}

/* --- Corner Brackets --- */
.corner-bracket {
    position: absolute;
    width: 40px; height: 40px;
    border: 2px solid var(--color-secondary);
    opacity: 0.3;
    pointer-events: none;
    z-index: 13;
}

.bracket-tl { top: 5%; left: 5%; border-right: none; border-bottom: none; }
.bracket-tr { top: 5%; right: 5%; border-left: none; border-bottom: none; }
.bracket-bl { bottom: 5%; left: 5%; border-right: none; border-top: none; }
.bracket-br { bottom: 5%; right: 5%; border-left: none; border-top: none; }

/* --- Glitch Lines --- */
.glitch-line {
    position: absolute;
    height: 1px;
    background: var(--color-glitch);
    opacity: 0;
    pointer-events: none;
    z-index: 14;
    animation: glitch-appear 5s ease-in-out infinite;
}

.glitch-line-1 { top: 35%; left: 0; width: 30%; animation-delay: 0s; }
.glitch-line-2 { top: 65%; right: 0; width: 40%; animation-delay: 2.5s; }

@keyframes glitch-appear {
    0%, 90%, 100% { opacity: 0; }
    92%, 94%, 96% { opacity: 0.6; }
    93%, 95% { opacity: 0; }
}

/* --- NEW: Circuit Board Traces --- */
.circuit-trace {
    position: absolute;
    pointer-events: none;
    z-index: 7;
    opacity: 0.2;
}

.trace-1 {
    top: 20%; left: 15%;
    width: 150px; height: 100px;
    border-left: 1px solid var(--color-secondary);
    border-top: 1px solid var(--color-secondary);
    animation: trace-glow 3s ease-in-out infinite;
}

.trace-1::before {
    content: '';
    position: absolute;
    top: 0; left: 0;
    width: 6px; height: 6px;
    background: var(--color-glitch);
    border-radius: 50%;
    box-shadow: 0 0 8px var(--color-glitch);
}

.trace-2 {
    bottom: 25%; right: 20%;
    width: 120px; height: 80px;
    border-right: 1px solid var(--color-secondary);
    border-bottom: 1px solid var(--color-secondary);
    animation: trace-glow 3s ease-in-out infinite 1.5s;
}

.trace-2::before {
    content: '';
    position: absolute;
    bottom: 0; right: 0;
    width: 6px; height: 6px;
    background: var(--color-glitch);
    border-radius: 50%;
    box-shadow: 0 0 8px var(--color-glitch);
}

@keyframes trace-glow {
    0%, 100% { opacity: 0.2; }
    50% { opacity: 0.4; }
}

/* --- NEW: Loading Bars --- */
.loading-bar {
    position: absolute;
    width: 180px;
    height: 20px;
    background: rgba(155, 161, 178, 0.1);
    border: 1px solid var(--color-secondary);
    pointer-events: none;
    z-index: 11;
    opacity: 0.3;
}

.loading-bar::before {
    content: '';
    position: absolute;
    left: 0; top: 0;
    height: 100%;
    background: linear-gradient(90deg, 
        transparent 0%,
        var(--color-glitch) 50%,
        transparent 100%);
    animation: loading-progress 3s ease-in-out infinite;
}

.loader-1 {
    top: 28%; right: 10%;
}

.loader-1::before {
    width: 70%;
}

.loader-2 {
    bottom: 35%; left: 12%;
    animation-delay: 1.5s;
}

.loader-2::before {
    width: 45%;
    animation-delay: 1.5s;
}

@keyframes loading-progress {
    0%, 100% { opacity: 0.3; transform: translateX(-20%); }
    50% { opacity: 0.6; transform: translateX(20%); }
}

/* --- NEW: CD/Holographic Disc --- */
.cd-disc {
    position: absolute;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: radial-gradient(circle at 50% 50%, 
        transparent 30%,
        rgba(186, 186, 189, 0.2) 35%,
        transparent 40%);
    border: 1px solid var(--color-secondary);
    pointer-events: none;
    z-index: 8;
    opacity: 0.25;
    animation: cd-spin 8s linear infinite, holographic-shimmer 4s ease-in-out infinite;
    background-blend-mode: screen;
}

.cd-1 {
    top: 40%; left: 8%;
}

.cd-2 {
    bottom: 18%; right: 15%;
    animation-delay: 4s, 2s;
}

@keyframes cd-spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@keyframes holographic-shimmer {
    0%, 100% { 
        box-shadow: 0 0 20px rgba(247, 248, 248, 0.3),
                    inset 0 0 20px rgba(186, 186, 189, 0.2);
    }
    50% { 
        box-shadow: 0 0 30px rgba(247, 248, 248, 0.5),
                    inset 0 0 30px rgba(186, 186, 189, 0.3);
    }
}

/* --- NEW: Barcode --- */
.barcode {
    position: absolute;
    display: flex;
    gap: 2px;
    pointer-events: none;
    z-index: 9;
    opacity: 0.25;
    animation: barcode-scan 4s ease-in-out infinite;
}

.barcode span {
    width: 3px;
    height: 60px;
    background: var(--color-text);
}

.barcode span:nth-child(even) {
    width: 2px;
}

.barcode span:nth-child(3n) {
    width: 5px;
}

.barcode-1 {
    top: 60%; right: 6%;
    transform: rotate(-5deg);
}

@keyframes barcode-scan {
    0%, 100% { opacity: 0.25; }
    50% { opacity: 0.4; }
}

/* --- NEW: System Messages --- */
.sys-message {
    position: absolute;
    font-size: 0.7em;
    color: var(--color-text);
    border: 1px solid var(--color-secondary);
    padding: 8px 12px;
    background: rgba(155, 161, 178, 0.05);
    pointer-events: none;
    z-index: 12;
    opacity: 0;
    animation: message-blink 6s ease-in-out infinite;
    letter-spacing: 0.05em;
}

.msg-1 {
    top: 15%; left: 25%;
    animation-delay: 0s;
}

.msg-2 {
    bottom: 20%; right: 22%;
    animation-delay: 3s;
}

@keyframes message-blink {
    0%, 90%, 100% { opacity: 0; }
    10%, 85% { opacity: 0.4; }
}

/* --- NEW: Wireframe Globe --- */
.globe-wireframe {
    position: absolute;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid var(--color-secondary);
    top: 50%;
    right: 10%;
    pointer-events: none;
    z-index: 8;
    opacity: 0.2;
    animation: globe-rotate 20s linear infinite;
}

.globe-wireframe::before,
.globe-wireframe::after {
    content: '';
    position: absolute;
    border: 1px solid var(--color-secondary);
}

.globe-wireframe::before {
    width: 100%;
    height: 30%;
    top: 35%;
    left: 0;
    border-radius: 50%;
}

.globe-wireframe::after {
    width: 1px;
    height: 100%;
    left: 50%;
    top: 0;
}

@keyframes globe-rotate {
    0% { transform: rotateY(0deg); }
    100% { transform: rotateY(360deg); }
}

/* --- NEW: Radar Sweep --- */
.radar {
    position: absolute;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid var(--color-secondary);
    bottom: 40%;
    left: 20%;
    pointer-events: none;
    z-index: 9;
    opacity: 0.25;
}

.radar::before {
    content: '';
    position: absolute;
    width: 50%;
    height: 2px;
    background: linear-gradient(90deg, var(--color-glitch), transparent);
    top: 50%;
    left: 50%;
    transform-origin: left center;
    animation: radar-sweep 4s linear infinite;
}

.radar::after {
    content: '';
    position: absolute;
    width: 4px;
    height: 4px;
    background: var(--color-glitch);
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 8px var(--color-glitch);
}

@keyframes radar-sweep {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* --- Content Styling --- */
.content-wrapper { 
    position: relative; 
    z-index: 20; 
    padding: 20px;
}

.id-text {
    font-size: 8vw;
    margin: 0;
    letter-spacing: 0.3em;
    text-shadow: 0 0 5px var(--color-text), 0 0 10px var(--color-secondary);
    transition: color 0.1s ease-in-out;
}

.id-text:hover { color: var(--color-glitch); }

.moto-text {
    font-size: 2vw;
    margin-top: 10px;
    opacity: 0.9;
    border-top: 1px solid var(--color-secondary);
    padding-top: 15px;
    animation: text-shift 4s infinite alternate;
}

@keyframes text-shift {
    0% { transform: translate(0, 0); }
    5% { transform: translate(1px, -1px); opacity: 0.85; }
    10% { transform: translate(-1px, 1px); opacity: 0.95; }
    15% { transform: translate(0, 0); opacity: 0.9; }
    100% { transform: translate(0, 0); }
}

/* --- Scroll Indicator --- */
.scroll-indicator {
    position: absolute; 
    bottom: 30px; 
    left: 50%;
    transform: translateX(-50%);
    z-index: 20; 
    opacity: 0.8;
    animation: bounce 2s infinite, static-flicker 0.2s infinite;
}

.down-arrow {
    width: 0; height: 0;
    border-left: 10px solid transparent; 
    border-right: 10px solid transparent;
    border-top: 15px solid var(--color-text);
    margin: 5px auto 0;
}

.glitch-text { font-size: 0.9em; letter-spacing: 0.1em; }

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: translateX(-50%) translateY(0); }
    40% { transform: translateX(-50%) translateY(-7px); }
    60% { transform: translateX(-50%) translateY(-3px); }
}

/* --- NEW: Additional Geometric Shapes --- */
.hexagon-1 {
    width: 100px;
    height: 57.74px;
    background: transparent;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    border-bottom: 86.6px solid var(--color-secondary);
    position: absolute;
    top: 30%;
    right: 15%;
    opacity: 0.15;
    animation: float-1 10s ease-in-out infinite;
}

.hexagon-1::before {
    content: "";
    position: absolute;
    width: 0;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    border-top: 86.6px solid var(--color-secondary);
    top: 86.6px;
    left: -50px;
}

.hexagon-2 {
    width: 80px;
    height: 46px;
    background: transparent;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 69.3px solid var(--color-secondary);
    position: absolute;
    bottom: 30%;
    right: 8%;
    opacity: 0.12;
    animation: rotate-slow 25s linear infinite;
}

.rectangle-1 {
    width: 180px;
    height: 80px;
    border: 1px solid var(--color-secondary);
    top: 18%;
    left: 25%;
    opacity: 0.18;
    animation: pulse-shape 5s ease-in-out infinite;
}

.pentagon {
    width: 90px;
    height: 90px;
    position: absolute;
    bottom: 20%;
    left: 15%;
    opacity: 0.15;
    clip-path: polygon(50% 0%, 100% 38%, 82% 100%, 18% 100%, 0% 38%);
    border: 1px solid var(--color-secondary);
    animation: float-2 9s ease-in-out infinite;
}

.circle-3 {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    top: 50%;
    left: 10%;
    box-shadow: 0 0 15px rgba(155, 161, 178, 0.2);
    animation: float-1 12s ease-in-out infinite;
}

/* --- About Section Styles --- */
.about-content {
    max-width: 800px;
}

.section-title {
    font-size: 4vw;
    letter-spacing: 0.5em;
    margin-bottom: 40px;
    text-shadow: 0 0 5px var(--color-text), 0 0 10px var(--color-secondary);
    border-bottom: 1px solid var(--color-secondary);
    padding-bottom: 15px;
}

.info-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 40px;
}

.info-item {
    display: flex;
    flex-direction: column;
    padding: 15px;
    border: 1px solid var(--color-secondary);
    background: rgba(155, 161, 178, 0.05);
    transition: all 0.3s ease;
}

.info-item:hover {
    background: rgba(155, 161, 178, 0.1);
    box-shadow: 0 0 15px rgba(155, 161, 178, 0.3);
}

.info-label {
    font-size: 0.8em;
    opacity: 0.7;
    letter-spacing: 0.2em;
    margin-bottom: 8px;
}

.info-value {
    font-size: 1.2em;
    letter-spacing: 0.1em;
    text-shadow: 0 0 3px var(--color-text);
}

.about-text {
    font-size: 1.1em;
    line-height: 1.8;
    opacity: 0.9;
    text-align: center;
    letter-spacing: 0.05em;
    padding: 20px;
    border-top: 1px solid var(--color-secondary);
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .info-grid {
        grid-template-columns: 1fr;
    }
    
    .section-title {
        font-size: 6vw;
    }
}

.nav-buttons {
    margin-top: 2em;
    display: flex;
    gap: 20px;
    justify-content: center;
}

.cyber-btn {
    position: relative;
    display: inline-block;
    padding: 12px 28px;
    color: var(--color-text);
    font-family: var(--font-mono);
    font-weight: bold;
    font-size: 1em;
    text-decoration: none;
    border: 2px solid var(--color-secondary);
    border-radius: 4px;
    background: rgba(155, 161, 178, 0.05);
    box-shadow: 0 0 10px rgba(155, 161, 178, 0.2);
    transition: all 0.3s ease-in-out;
}

.cyber-btn:hover {
    color: var(--color-glitch);
    border-color: var(--color-glitch);
    box-shadow: 0 0 20px var(--color-glitch), 0 0 40px rgba(247, 248, 248, 0.2);
    transform: translateY(-2px) scale(1.05);
}

.cyber-btn::after {
    content: '';
    position: absolute;
    top: -2px; left: -2px; right: -2px; bottom: -2px;
    border: 1px solid var(--color-secondary);
    pointer-events: none;
    opacity: 0.5;
    mix-blend-mode: screen;
    border-radius: 4px;
}
